<template>
  <Transition name="modal">
    <div class="modal" @click="closeModal">
      <div class="modal__block" @click.stop="">
        <h2 class="modal__block-title">
          {{ edit ? $t('edit') : $t('title') }}
        </h2>
        <div class="modal__block-inputs">
          <label for="inp">
            <span>Title</span>
            <input id="inp" type="text" v-model="title" />
          </label>
          <label for="area">
            <span>Content</span>
            <textarea id="area" v-model="text"></textarea>
          </label>
        </div>
        <div class="modal__block-btns">
          <button class="modal__block-btns-btn del" @click="closeModal">
            Отмена
          </button>
          <button
            v-if="!edit"
            class="modal__block-btns-btn edit"
            @click="addNote"
          >
            Добавить
          </button>
          <button v-else class="modal__block-btns-btn edit" @click="changeNote">
            Изменить
          </button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
import { v4 as uuidv4 } from "uuid";
export default {
  props: {
    edit: {
      typeof: Boolean,
    },
    editeNote: {
      typeof: Object,
    },
  },
  data() {
    return {
      text: "",
      title: "",
    };
  },
  methods: {
    closeModal() {
      this.$emit("close");
      this.text = this.title = "";
    },
    addNote() {
      if (this.text != "" && this.title != "") {
        const note = {
          id: uuidv4(),
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("addNote", note);
        this.closeModal();
      }
    },
    changeNote() {
      if (this.text != "" && this.title != "") {
        const newNote = {
          id: this.editeNote.id,
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        };
        this.$emit('changedNote', newNote)
        this.closeModal();
      }
    },
  },
};
</script>

<style>
.modal-enter-active,
.modal-leave-active {
  transition: all 0.3s ease;
}

.modal-enter-from,
.modal-leave-to {
  opacity: 0;
  transform: scale(1.3);
}
</style>