<template>
  <div class="wrapper">
    <Navbar @setSearch="serachValue = $event" />
    <Notes :notes="filterNotes" @delNote="delNote" @change="change" />
    <Modal
      v-show="isModalOpen"
      @close="isModalOpen = false"
      @addNote="addNote"
      :edit="edit"
      :editeNote="editeNote"
      @changedNote="changedNote"
    />
    <button class="add__note" @click="(isModalOpen = true), (edit = false)">
      <img src="@/assets/images/edit.svg" alt="" />
    </button>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Notes from "@/components/Notes.vue";
import Modal from "@/components/Modal.vue";
export default {
  components: {
    Navbar,
    Notes,
    Modal,
  },
  data() {
    return {
      notes: [],
      isModalOpen: false,
      edit: false,
      editeNote: null,
      serachValue: "",
    };
  },
  methods: {
    addNote(note) {
      this.notes.push(note);
    },
    getNotes() {
      let localNotes = localStorage.notes;
      if (localNotes) {
        this.notes = JSON.parse(localNotes);
      }
    },
    delNote(id) {
      let index = this.notes.findIndex((el) => el.id == id);
      this.notes.splice(index, 1);
    },
    change(id) {
      this.isModalOpen = this.edit = true;
      let currentNote = this.notes.find((el) => el.id == id);
      this.editeNote = currentNote;
    },
    changedNote(newNote) {
      this.notes.forEach((note) => {
        if (note.id == newNote.id) {
          note.title = newNote.title;
          note.text = newNote.text;
          note.date = newNote.date;
        }
      });
    },
  },
  computed: {
    filterNotes() {
      return this.serachValue
        ? this.notes.filter((note) =>
            note.title.toLowerCase().includes(this.serachValue.toLowerCase())
          )
        : this.notes;
    },
  },
  created() {
    this.getNotes();
  },
  watch: {
    notes: {
      handler() {
        localStorage.notes = JSON.stringify(this.notes);
      },
      deep: true,
    },
  },
};
</script>

